.cart-items {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 20rem;
  overflow: auto;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.actions {
  text-align: right;
  display: flex;
  float: right;
}

.close-btn button {
  background-color: white;
  color:#363633;
  border: 2px solid #363633;
  margin-right: 1em
}

.btn button {
  color:white;
  border: 2px solid #363633;
  margin-right: 0;
}

.actions button:hover {
    background-color: #FEEC50;
    color:#363633;
    border: 2px solid #363633;
}
